import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Link } from "react-router-dom";
import moment from "moment";

import notification from "../../../../components/notification";
import {
  Empty,
  Tooltip,
  Row,
  Col,
  DatePicker,
  Select,
  Collapse
  //Input
} from "antd";

// import Box from '../../components/utility/box';
// import HelperText from "../../../../components/utility/helper-text";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/currency/currencyRate/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, {
  Box,
  // StatusTag,
  customCollapseStyle
} from "../../../App/app.style";
import NumberFormat from "../../../App/NumberFormat";

const Panel = Collapse.Panel;
const Option = Select.Option;
// const Search = Input.Search;
const RangePicker = DatePicker.RangePicker;
const redirectTo = "/dashboard/setup/menus";

class CurrencyRate extends Component {
  state = {
    filter: {
      id_currency: null,
      start_date: null,
      end_date: null,
      pagination: null
    },
    search: null
  };

  // Get All data diawal
  componentDidMount() {
    const { getCurrencyRates } = this.props;
    getCurrencyRates();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.currencyRate.error) {
      notification("error", this.props.currencyRate.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.currencyRate.success) {
      notification("success", this.props.currencyRate.success);
      this.props.clearSuccess();
    }
  }

  // Handle search
  handleSearch = value => {
    const { getCurrencyRates } = this.props;
    getCurrencyRates(1, value);
  };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;

    this.setState({ search: value });
  };

  // Handle Reset search
  handleResetSearch = () => {
    const { getCurrencyRates } = this.props;
    this.setState({
      filter: {
        id_currency: null,
        start_date: null,
        end_date: null,
        pagination: null
      }
    });
    this.setState({ search: null });
    getCurrencyRates();
  };

  //Handle filter change
  handleFilterChange = (name, value) => {
    if (name === "date" && value[0] && value[1]) {
      const start = value[0].format("YYYY-MM-DD");
      const end = value[1].format("YYYY-MM-DD");
      console.log(start, end);
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: start,
          end_date: end
        }
      });
    } else if (name === "date" && !value[0] && !value[1]) {
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: null,
          end_date: null
        }
      });
    } else {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value
        }
      });
    }
  };

  // Handle filter
  handleFilter = () => {
    const { getCurrencyRates } = this.props;
    //const { current_page } = this.props.item;
    const { filter } = this.state;
    let string = "";

    if (filter.id_currency) {
      string += "id_currency=" + filter.id_currency;
    }
    if (filter.start_date && filter.end_date) {
      string
        ? (string +=
            "&start_date=" + filter.start_date + "&end_date=" + filter.end_date)
        : (string +=
            "start_date=" + filter.start_date + "&end_date=" + filter.end_date);
    }
    if (filter.pagination) {
      string
        ? (string += "&pagination=" + filter.pagination)
        : (string += "pagination=" + filter.pagination);
    }

    this.setState({ search: null });
    getCurrencyRates(1, null, string);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getCurrencyRates } = this.props;
    this.setState({
      filter: {
        id_currency: null,
        start_date: null,
        end_date: null,
        pagination: null
      }
    });
    this.setState({ search: null });
    getCurrencyRates();
  };

  // Handle action di table
  handleRecord = (action, id) => {
    if (action === "activate") {
      this.props.activateCurrencyRate(id);
    } else {
      this.props.deactivateCurrencyRate(id);
    }
  };

  // Handle pagination
  handlePageChange = page => {
    const { getCurrencyRates } = this.props;
    const { search } = this.props.currencyRate;
    const { filter } = this.props.currencyRate;
    getCurrencyRates(page, search, filter);
  };

  // Definisi kolom2 serta field2 API yang dipakai tabel
  columns = [
    {
      title: "Currency Code",
      dataIndex: "currency.currency_code",
      rowKey: "currency.currency_code",
      width: "20%"
    },
    {
      title: "Currency Name",
      dataIndex: "currency.currency_name",
      rowKey: "currency.currency_name",
      width: "30%"
    },
    {
      title: "Rate Date",
      dataIndex: "rate_date",
      rowKey: "rate_date",
      width: "25%",
      render: text => <span>{moment(text).format("DD MMMM YYYY")}</span>
    },
    {
      title: "Rate (to Base)",
      dataIndex: "rate",
      rowKey: "rate",
      width: "25%",
      align: "right",
      render: text => <NumberFormat value={text} />
    }
  ];

  listAction = {
    title: "",
    dataIndex: "id_currency_rate",
    rowKey: "id_currency_rate",
    width: "10%",
    align: "center",
    render: (text, row) => (
      <div>
        <Tooltip
          placement="topLeft"
          title={<IntlMessages id="tooltip.update" />}
        >
          <Link to={`${this.props.match.path}/update/${text}`}>
            <Button currency_rate="small" className="invoiceEdtBtn">
              <i className="ion-android-create" />
            </Button>
          </Link>
        </Tooltip>
      </div>
    )
  };

  render() {
    // console.log(this.props.currencyRate);
    // Data setup
    const {
      currency_rates,
      loading,
      currencies,
      page_size,
      total,
      current_page
    } = this.props.currencyRate;
    // console.log(currency_rates); //console.log(this.state);
    const dataSource = [];
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    let base_currency = currencies
      ? currencies.find(base => base.is_base === 1)
      : null;

    //console.log(permissions);
    // if (
    //   permissions.includes("Update Currency Rate") &&
    //   this.columns.length === 4
    // ) {
    //   this.columns.push(this.listAction);
    // }

    currency_rates.map((currency_rate, index) => {
      return dataSource.push({
        ...currency_rate,
        key: index + 1
      });
    });

    // Pagination Setup
    //console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="currencyRates.pageHeader" />}
            marginBottom={true}
          >
            <div style={{ display: "flex", justifyContent: "flex-end" }}>
              {/* {permissions.includes("Create Currency Rate") ? (
                <Link to={`${this.props.match.path}/add`}>
                  <Button type="primary" className="mateAddInvoiceBtn">
                    <IntlMessages id="currencyRates.button.add" />
                  </Button>
                </Link>
              ) : null} */}
              <Button
                onClick={() => this.props.history.replace(redirectTo)}
                style={{ marginLeft: "5px" }}
              >
                <IntlMessages id="button.back" />
              </Button>
            </div>
          </PageHeader>

          <div className="isoFilter">
            <Collapse style={customCollapseStyle}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "24px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="rangeDate" />
                    </label>
                    <br />
                    <RangePicker
                      format="DD MMMM YYYY"
                      showTime
                      allowClear={true}
                      style={{ width: "95%" }}
                      value={
                        this.state.filter.start_date &&
                        this.state.filter.end_date
                          ? [
                              moment(
                                this.state.filter.start_date,
                                "YYYY-MM-DD"
                              ),
                              moment(this.state.filter.end_date, "YYYY-MM-DD")
                            ]
                          : []
                      }
                      onChange={this.handleFilterChange.bind(this, "date")}
                    />
                  </Col>
                  <Col span={12}>
                    <label>
                      <IntlMessages id="currencies.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_currency"
                      )}
                      value={
                        this.state.filter.id_currency
                          ? this.state.filter.id_currency
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={<IntlMessages id="select.currency" />}
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loading}
                      allowClear={true}
                      //showArrow={false}
                    >
                      {currencies.map((data, key) =>
                        data.is_base === 0 ? (
                          <Option key={key} value={data.id_currency}>
                            {`${data.currency_code} (${data.currency_name})`}
                          </Option>
                        ) : (
                          0
                        )
                      )}
                    </Select>
                  </Col>
                </Row>
                <Row>
                  <Col span={12}>
                    <Col span={8}>
                      <label>
                        <IntlMessages id="dataPerPage" />
                      </label>
                      <br />
                      <Select
                        showSearch
                        onChange={this.handleFilterChange.bind(
                          this,
                          "pagination"
                        )}
                        value={
                          this.state.filter.pagination
                            ? this.state.filter.pagination
                            : page_size
                        }
                        style={{ width: "80%" }}
                        // placeholder={
                        //   <IntlMessages id="pettyCashTypes.filter.select.transactionType" />
                        // }
                        optionFilterProp="children"
                        filterOption={true}
                        //allowClear={true}
                        //showArrow={false}
                      >
                        <Option key={1} value={10}>
                          10
                        </Option>
                        <Option key={2} value={25}>
                          25
                        </Option>
                        <Option key={3} value={50}>
                          50
                        </Option>
                        <Option key={4} value={100}>
                          100
                        </Option>
                      </Select>
                    </Col>
                    <Col span={12} style={{ marginTop: "22px" }}>
                      <Button
                        size="small"
                        type="primary"
                        style={{ marginRight: "10px" }}
                        onClick={this.handleFilter}
                      >
                        <IntlMessages id="filter.only" />
                      </Button>
                      <Button onClick={this.handleResetFilter} size="small">
                        <IntlMessages id="filter.reset" />
                      </Button>
                    </Col>
                  </Col>
                </Row>
              </Panel>
            </Collapse>
          </div>

          <div className="isoFilter">
            <span style={{ fontSize: "16px" }}>
              Base Currency :{" "}
              {base_currency
                ? `${base_currency.currency_code} (${base_currency.currency_name})`
                : null}
            </span>
          </div>

          <CardWrapper>
            {currency_rates.length === 0 && !loading ? (
              <Empty
                description={<IntlMessages id="currencyRates.table.noData" />}
              />
            ) : (
              <div className="isoInvoiceTable">
                <Scrollbars style={{ width: "100%" }}>
                  <TableWrapper
                    dataSource={dataSource}
                    columns={this.columns}
                    loading={this.props.currencyRate.loading}
                    className="invoiceListTable"
                    bordered={true}
                    // Set pagination, jika ada page_size mengunakan pagination API
                    // jika tidak ada page_size menggunakan pagination bawaan
                    pagination={
                      page_size
                        ? {
                            current: current_page,
                            pageCurrencyRate: parseInt(page_size),
                            total: total,
                            onChange: this.handlePageChange.bind(this)
                          }
                        : true
                    }
                  />
                </Scrollbars>
              </div>
            )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    currencyRate: state.currencyRate,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(CurrencyRate);
